html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow-bottom {
  width: 1.03em;
  height: 1em;
  fill: #D50000;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "Open Sans Regular";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans Semibold";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans Bold";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font: 400 18px/1.4 'Open Sans Regular', sans-serif;
  color: #959595;
  background-color: #d50000;
}

a {
  text-decoration: none;
  color: #fff;
}

.btn {
  font: 400 20px/1.4 'Open Sans Bold', sans-serif;
  text-align: center;
  border-radius: 30px;
  display: inline-block;
  background-color: #d50000;
  padding: 7px 59px;
  transition: background-color 0.25s;
}

.btn:hover {
  background-color: #ef0000;
}

.btn-scroll {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  padding: 10px;
}

.btn-scroll:hover .icon {
  transform: translateY(5px);
}

.btn-scroll .icon {
  font-size: 20px;
  color: #d50000;
  transition: transform 0.25s;
}

.title {
  font: 400 46px/1.2 'Open Sans Regular', sans-serif;
  color: #fff;
}

.inner {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}

header {
  padding: 60px 0 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
}

header .inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  background: url(../img/logo.svg) no-repeat center;
  width: 180px;
  height: 85px;
}

.nav {
  display: flex;
}

.nav__link {
  padding: 10px 28px;
  display: block;
  font-family: 'Open Sans Semibold', sans-serif;
  transition: color 0.25s;
  position: relative;
  overflow: hidden;
}

.nav__link:after {
  content: '';
  display: block;
  left: 28px;
  bottom: 4px;
  width: calc(100% - 56px);
  height: 2px;
  background-color: #d50000;
  position: absolute;
  transform: scaleX(0);
  transition: transform 0.25s;
}

.nav__link:hover {
  color: #d50000;
}

.nav__link:hover:after {
  transform: scaleX(1);
}

.nav__link:last-of-type {
  padding: 10px 0 10px 28px;
}

.nav__link:last-of-type:after {
  right: 0;
  width: calc(100% - 28px);
}

.company {
  background-color: #000;
}

.company .inner {
  height: 100vh;
  min-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.company .btn-scroll {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10%;
}

.company-content {
  padding: 80px 0 0;
}

.company-content__title {
  text-align: center;
  color: #fff;
  font-size: 46px;
  line-height: 1.4;
}

.company-content__btn {
  padding: 55px 0 0;
}

.company-content__bottom {
  padding: 22px 0 0;
  color: #d9d9d9;
}

.services {
  text-align: center;
  padding: 50px 0;
  background-color: #fff;
}

.services .title {
  color: #424242;
}

.services-list {
  padding: 70px 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.services-list__item {
  width: 32%;
  min-height: 456px;
}

@media only screen and (max-width: 900px) {
  .services-list__item {
    width: 49%;
    margin: 2% 0 0;
  }
}

@media only screen and (max-width: 600px) {
  .services-list__item {
    width: 100%;
    min-height: auto;
  }
}

.s-item {
  background-color: #fff;
  padding: 18px 18px 30px;
  box-shadow: 0px 10px 40px #D9D9D9;
  border-radius: 3px;
}

.s-item__ico {
  width: 160px;
  height: 160px;
  margin: auto;
}

.s-item__title {
  margin: 8px 0 0;
  font: 20px/26px 'Open Sans Bold', sans-serif;
  color: #4f4f4f;
  min-height: 78px;
}

.s-item__txt {
  margin: 26px 0 0;
  color: #959595;
  font: 18px/28px 'Open Sans Regular', sans-serif;
}
